/**
 * TextInput.scss
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';
 @import '../forms';

.text-input-container-v3 {
  @extend %input-container;
  min-height: $input-height;

  &.textarea-padding {
    padding-bottom: 20px;
  }

  &.read-only,
  &:hover,
  &:focus {
    @extend %input-container-focus;

    .title-header {
      @extend %title-header-focus;
    }
  }

  .title-header {
    @extend %title-header;
  }

  .focused-title {
    @extend %title-header-focus;
  }

  .text-input {
    @extend %input-field-container;
    

    &.absolute-input {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
    }

    .text {
      @extend %input-field; 
    }

    textarea {
      height: 100%;
      width: 100%;
      max-height: none !important;
  
      background-color: transparent;
      border: none;
      overflow: auto;
      outline: none;
  
      box-shadow: none;

      font-size: $font-size-regular;
      font-weight: $font-weight-normal;
      color: $color-title;

      &::placeholder {
        color: $color-line;
      }

      &:focus {
        outline: none;
      }
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        /* display: none; <- Crashes Chrome on hover */
        -webkit-appearance: none;
        margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
    }

    input[type=number] {
        -moz-appearance:textfield; /* Firefox */
    }
  }

  .postcode {
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    height: 100%;
    margin-right: 15px;
    position: absolute;

    &:hover {
      img {
        filter: $color-filter-hover-alt;
      }
    }

    &:active {
      img {
        filter: $color-filter-hover;
      }
    }
  }
}
