@import '../../../base';

.business-main-container {
  %description {
    font-weight: $font-weight-semibold;
    font-size: $font-size-smallest;
    color: $color-input-form-landing-desc2;
  }

  .margin-padding {
    max-width: 1110px;
    padding: 0 40px;
    margin: auto;
    
    @media screen and (max-width: 991px) {
      padding: 0 15px;
    }
  }

  .carousel-container {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (max-width: 991px) {
      margin-bottom: 20px;
    }

    .insurer-carousel-label {
      color: $color-input-text;
      font-size: $font-size-smallest;
      font-weight: $font-weight-bold;
      margin-bottom: -20px;
    }
    .insurer-logo-carousel {
      max-width: 650px;
      width: 100%;
      display: flex;
      justify-content: center;
      
      transform: translateY(-30px);

      .logo {
        height: 50px;
        display: flex;
        img {
          margin: auto;
        }
      }
    }
  }

  .text-link {
    margin-left: 3px;
    font-size: $font-size-smallest;
    font-weight: $font-weight-bold;
    color: $color-hover;

    &:hover {
      text-decoration: underline;
    }
  }

  .slider-container {
    .slick-slider {
      width: calc(100vw);
      transform: translateX(-15px);
      overflow: hidden;
      padding-bottom: 28px;
  
      .slick-dots {
        position: relative;
        transform: translateY(-28px);
        z-index: 1;
      }
    }
  }
  
}
