.email-input-container {
  .email-input-inner-container {
    border: 1px solid #CADBEA;
    box-sizing: border-box;
    border-radius: 40px;
    transform: translateX(-5px);
    overflow: hidden;

    @media screen and (max-width: 767px) {
      margin-left: 10px;
    }

    .text-input {
      margin-left: 10px;
      display: flex;
      justify-content: center;
      align-items: center;

      input {
        font-weight: normal;
        line-height: normal;
        font-size: 12px;

        &::placeholder {
          color: #B6BDCB;
        }

        border: none;
        box-shadow: none;
      }

      button {
        padding: 0;

        p {
          font-size: 15px;
        }
      }
    }
  }

  @media screen and (max-width: 575px) {
    .promo-button {
      margin-top: 10px;
      justify-content: center;
    }

    .input-wrapper-error-text {
      margin-top: 5px;
      align-items: center;
      justify-content: center;
    }
  }
}
