@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .main-container {
    .content {
      max-width: 600px;
      margin: auto;
    }

    h1 {
      font-weight: $font-weight-heavy;
      color: $color-input-form-landing-desc;
    }
    h2 {
      font-size: $font-size-big;
      font-weight: $font-weight-semibold;
      color: $color-input-text;
      margin-bottom: 20px;

      @media screen and (max-width: 767px) {
        font-size: $font-size-regular;
        margin-bottom: 30px;
      }
    }
    h6 {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
    }
    h6.footnote {
      margin-top: 15px;
    }

    .response-container {
      justify-content: center;
      background-color: #F9FBFF;
      padding-top: 5px;
      padding-bottom: 5px;
      border-radius: 15px;

      .response-label {
        align-self: center;
        padding-left: 5px;
        margin-bottom: 0px;
      }
    }

    .main-img {
      max-width: 650px;
      margin: auto;
      img {
        width: 100%;
        height: auto;
      }
    }

    .learn-more-button {
      margin-top: 10px;
      @media screen and (max-width: 991px) {
        display: flex;
        justify-content: center;
      }
    }

    @media screen and (max-width: 767px) {
      h6.footnote {
        margin-top: 20px;
      }
      img {
        margin: 0;
      }
    }
    @media screen and (max-width: 991px) {
      h1, h6 {
        text-align: center;
      }
    }
  }
}
